.ekko-lightbox-container {
	position: relative;
}

.ekko-lightbox-nav-overlay {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 100;
	width: 100%;
	height: 100%;
}

.ekko-lightbox-nav-overlay a {
	z-index: 100;
	display: block;
	width: 49%;
	height: 100%;
	font-size: 30px;
	color: #fff;
	text-shadow: 2px 2px 4px #000;
	opacity: 0;
	filter: dropshadow(color=#000000, offx=2, offy=2);
	-webkit-transition: opacity 0.5s;
	-moz-transition: opacity 0.5s;
	-o-transition: opacity 0.5s;
	transition: opacity 0.5s;
}

.ekko-lightbox-nav-overlay a:empty {
	width: 49%;
}

.ekko-lightbox a:hover {
	text-decoration: none;
	opacity: 1;
}

.ekko-lightbox .glyphicon-chevron-left {
	left: 0;
	float: left;
	padding-left: 15px;
	text-align: left;
}

.ekko-lightbox .glyphicon-chevron-right {
	right: 0;
	float: right;
	padding-right: 15px;
	text-align: right;
}

.ekko-lightbox .modal-footer {
	text-align: left;
}
.modal-backdrop.in {
		-webkit-opacity: 0.95;
		-moz-opacity: 0.95;
		opacity: 0.95;
		background-color: #333333;
}
.ekko-lightbox .modal-content {
		-webkit-box-shadow: none;
		-moz-box-shadow: none;
		box-shadow: none;
		-webkit-border-radius: 0;
		-moz-border-radius: 0;
		border-radius: 0;
		border: none;
		background-color: transparent;
}
.ekko-lightbox .modal-header {
		border: 0;
		padding-left: 0;
		padding-right: 0;
}
.ekko-lightbox .modal-header .close {
		-webkit-opacity: 0.5;
		-moz-opacity: 0.5;
		opacity: 0.5;
		color: #fff;
		text-shadow: 0;
		font-weight: 100;
		margin-top: 5px;
}
.ekko-lightbox .modal-header .close:hover {
		-webkit-opacity: 1;
		-moz-opacity: 1;
		opacity: 1;
}
.ekko-lightbox .modal-header h4.modal-title {
		font-weight: 100;
		color: #fff;
		padding: 0;
}
.ekko-lightbox .modal-body {
		padding: 0;
}
.ekko-lightbox .modal-footer {
		-webkit-opacity: 0.9;
		-moz-opacity: 0.9;
		opacity: 0.9;
		border: 0;
		color: #fff;
		font-weight: 100;
		padding: 0;
}
.ekko-lightbox-nav-overlay a {
		-webkit-opacity: 0.9;
		-moz-opacity: 0.9;
		opacity: 0.9;
		text-shadow: none;
}
